import logo from './logo.svg';
import './App.css';

import WheelComponent from 'react-wheel-of-prizes'
import MyWheelComponent from './MyWheelComponent.js'
import { useState, useEffect } from 'react';
const def_colors = [];
for (let i = 0; i < 10; i++){
    def_colors.push(`#${Math.floor(Math.random() * 0x1000000).toString(16).padStart(6, 0)}`);
}
function getRandomInt(max) {
  return Math.floor(Math.random() * max);
}

function App() {
  const [items,setItems] = useState(JSON.parse(localStorage.getItem("items")) ?? [...Array(10).keys()].map(elem=>(elem+1).toString()));
  const [colors, setColors] = useState(JSON.parse(localStorage.getItem("colors")) ?? def_colors);
  const [time, setTime] = useState(getRandomInt(400)+250);
  const [winner, setWinner] = useState(localStorage.getItem("winner") ?? '');
  const [excluded,setExcluded]= useState(JSON.parse(localStorage.getItem("excluded")) ?? []);
  let vw = Math.max(document.documentElement.clientWidth || 0, window.innerWidth || 0);
  let vh = Math.max(document.documentElement.clientHeight || 0, window.innerHeight || 0);
  const onFinished = (winner) => {
    console.log(winner);
    let removed_ind = items.findIndex(item=>item===winner);
    console.log(removed_ind);
    console.log(items);
    if (items.length > 1){
      setItems(items.filter(((item,index)=>index!==removed_ind)));
      setColors(colors.filter(((item,index)=>index!==removed_ind)));
    }
    setTime(getRandomInt(400)+250);
    setWinner(winner);
    setExcluded([winner,...excluded]);
  };
  const restartGame = () => {
    localStorage.clear();
    setItems([...Array(10).keys()].map(elem=>(elem+1).toString()));
    setColors(def_colors);
    setWinner('');
    setExcluded([]);
  }
  useEffect(() => {
    console.log(items);
    localStorage.setItem("items", JSON.stringify(items));
    localStorage.setItem("colors", JSON.stringify(colors));
    localStorage.setItem("winner", winner);
    localStorage.setItem("excluded", JSON.stringify(excluded));

  },[items,colors,winner,excluded]);
  /*      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo" />
        <p>
          Edit <code>src/App.js</code> and save to reload.
        </p>
      </header>*/
  return (
    <div className="App">
      <div className="App-header">
        <div key={winner} className='text'>
        <h2></h2>
        <h4 className='animated-text'>{ items.length > 1 ? "" : " "}:</h4>
        <h3 className='animated-text'>{winner}</h3>
        {items.length === 1 && 
        <div>        
          <h4 className='animated-text'> :</h4>
          <h3 className='animated-text'>{items[0]}</h3>
        </div>} 
        <p>: </p>
        <p className='animated-text'>{excluded.join(', ')}</p>
        {items.length !== 10 && <button onClick={()=>restartGame()}> </button>}
        </div>
        {items.length > 1 && <div key={items} className='Wheel'>
          
        <MyWheelComponent
          key={items}
          segments={items}
          segColors={colors}
          onFinished={(winner) => onFinished(winner)}
          primaryColor='black'
          contrastColor='white'
          buttonText='!'
          isOnlyOnce={false}
          size={(vw < 600)? (vw+vh)/8:Math.max(vw,vh)/4}
          upDuration={time/(10*items.length)}
          downDuration={5*time/(items.length)}
          fontFamily='Arial'
      />
        
        </div>
      }
      </div>
    </div>
  );
}

export default App;
